.feedback-card{
  border:$border;
  padding: 48px;
  border-radius: 12px;
  background-color: $darkGray;
  &__img{
    width: 100%;
    max-width: 176px;
    margin: 0 auto;
  }
  &__avatar{
    position: relative;
    padding-bottom: 100%;
    border-radius: 50%;
    overflow: hidden;
    img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__title{
    text-align: center;
    margin-top: 24px;
  }
  &__socials{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 12px auto 0;
    width: 100%;
    max-width: 216px;
  }
  &__social{
    img{
      transition: all .3s;
    }
    &:hover{
      img{
        transform: translateY(-10px);
      }
    }
  }
  &__rating{
    display: flex;
    justify-content: center;
    margin-top: 28px;
  }
  &__desc{
    margin-top: 28px;
    color: $gray;
  }
  &__link{
    margin-top: 28px;
    width: 100%;
  }
}

.feedback-card2{
  padding: 24px 0;
  border-bottom: 1px solid $mediumDark;
  display: flex;
  align-items: center;
  &:first-child{
    border-top: 1px solid $mediumDark;
  }
  &__rating{
    flex-shrink: 0;
    margin-right: 40px;
  }
  &__name{
    font-weight: 500;
  }
  &__split{
    color: $secondary;
  }
  &__date{
    color: $gray;
  }
  &__desc{
    margin-top: 12px;
    max-width: 355px;
  }
}


@media screen and (max-width: 576px){
  .feedback-card2{
    display: block;
    &__rating{
      display: flex;
      margin: 0 0 25px;
    }
  }
}


@media screen and (max-width: 460px){
  .feedback-card{
    padding: 32px;
  }
}
