@import '../vars';
.modal {
    background: rgba($bg, 0.6);
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &--active{
        opacity: 1;
        pointer-events: unset;
    }
    &__dialog{
        background-color: $bg;
        width: 880px;
        max-width: 100vh;
        //overflow-y: auto;
        position: relative;
        border-radius: 12px;
        border: 2px solid $primary;
        background-image: url(../img/modal-bg.webp);
        background-size: cover;
        background-position:right center ;
        display: grid;
        grid-template-columns: 384px auto;
    }
    &__content{
        padding: 48px 60px;
    }
    &__title{
        font-size: 48px;
        line-height: 125%;
        margin-top: 44px;
    }
    .main-form{
        width: calc(100% + 4px);
        height: calc(100% + 4px);
        margin: -2px 0 0 -2px;
    }
}



@media screen and (max-width: 768px){
    .modal{
        &__dialog{
            grid-template-columns: 100%;
        }
        &__content{
            display: none;
        }
    }
}




