.contacts{
  &__wrap{
    display: grid;
    grid-template-columns: 385px auto;
    gap: 32px;
    margin-top: 40px;
  }
}

.main-form--contacts{
  .main-form{
    &__btn{
      max-width: 288px;
      margin-bottom: 0;
    }

  }
  .fg{
    input{
      max-width: 440px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .contacts {
    &__wrap {
      display: block;
    }

    &__sidebar {
      width: 100%;
      margin: 40px 0 0;
    }
  }

  .main-form--contacts{
    .main-form{
      &__btn{
        max-width: 100%;
      }
    }
    .fg{
      input{
        max-width: 100%;
      }
    }
  }
}
