.main{
  &__wrap{
    display: flex;
    justify-content: space-between;
    margin-top: 40px;
  }
  &__sidebar{
    width: 31.58%;
    min-width: 300px;
    flex-shrink: 0;
    margin-left: 32px;
  }
  &__content{
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
  }
  &__group{
    margin-top: 100px;
  }
  &__subtitle{
    margin-bottom: 40px;
  }
  &__banks,
  &__places{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-top: -32px;
  }
  &__place,
  &__bank{
    margin: 24px 32px 0 0;
    width: 280px;
    height: 56px;
    position: relative;
    input{
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      z-index: 2;
      width: 100%;
      height: 100%;
      cursor: pointer;
      &:checked+label{
        border-color: $warning;
      }
      &:disabled{
        pointer-events: none;
        &+label{
          background: #404056;
        }
      }
    }
    label{
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      font-weight: 500;
      font-size: 16px;
      background: $mediumDark;
      border-radius: 8px;
      padding: 8px;
      border: 1px solid transparent;
    }
    i{
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: $bg;
      border-radius: 50%;
      width: 40px;
      height: 40px;
      flex-shrink: 0;
      margin-right: 8px;
    }
  }
  &__places{
    &-wrap{
      margin-top: 40px;
    }
    &-title{
      margin-bottom: 12px;
    }
  }
  &__place{
    height: 48px;
    width: 176px;
    label{
      justify-content: center;
    }
  }
}

.exchange{
  width: calc(50% - 16px);
  background-color: $mediumDark;
  border-radius: 12px;
  &__top{
    padding: 40px 48px;
    background-color: $darkGray;
    border: $border;
    border-radius: 12px;
    min-height: 264px;
  }
  &__input{
    position: relative;
    input{
      padding-right: 52px;
    }
    &-icon{
      width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      background-color: $mediumDark;
      position: absolute;
      bottom: 8px;
      right: 8px;
    }
  }
  &__hint{
    margin-top: 16px;
    color: $gray;
  }
  &__bottom{
    padding: 48px;
  }
  &__item{
    min-height: 48px;
    position: relative;
    &:not(:last-child){
      margin-bottom: 12px;
    }
    input{
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      z-index: 2;
      width: 100%;
      height: 100%;
      cursor: pointer;
      &:checked+label{
        border-color: $warning;
      }
      &:disabled{
        pointer-events: none;
        &+label{
          //background: #404056;
        }
      }
      &:hover+label{
        img{
          animation-name: rotation2;
          animation-duration: 1s;
          animation-iteration-count: 1;
          animation-timing-function: linear;
        }
      }
    }
    label{
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      padding: 4px;
      font-weight: 500;
      font-size: 16px;
      background: $bg;
      border-radius: 50px;
      border: 1px solid transparent;
    }
    i{
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: $mediumDark;
      border-radius: 50%;
      width: 40px;
      height: 40px;
      flex-shrink: 0;
      margin-right: 8px;
      img{

      }
    }
  }
  &__select{
    margin-top: 24px;
    display: none;
    input{
      padding-left: 56px;
      background-image: url(../img/arrowDown.svg);
      background-repeat: no-repeat;
      background-position: right 16px center;
    }
    .exchange__input-icon{
      right: unset;
      left: 8px;
    }
  }
}

@keyframes rotation {
  0% {
    transform: rotate3d(0,1,0,0deg);
  }
  50% {
    transform: rotate3d(0,1,0,360deg);
  }
  100% {
    transform: rotate3d(0,1,0,720deg);
  }
}

@keyframes rotation2 {
  0% {
    transform: rotate3d(0,1,0,0deg);
  }
  100% {
    transform: rotate3d(0,1,0,360deg);
  }
}

.main-form{
  padding: 48px;
  background-color: $darkGray;
  border: $border;
  border-radius: 12px;
  &__title{
    margin-bottom: 40px;
  }
  .fg{
    margin-bottom: 24px;
  }
  &__btn{
    width: 100%;
    margin: 48px 0 24px;
  }
  &__check{
    margin-top: 16px;
  }
  &__hint{
    color: $gray2;
    margin-top: 24px;
    text-align: center;
    a{
      font-weight: 500;
      text-decoration-line: underline;
      color: $primary;
    }
  }
}

.condition{
  margin-top: 48px;
  &__desc{
    margin-top: 24px;
    color: $gray;
  }
}

@media screen and (max-width: 1200px){
  .main{
    &__wrap{
      display: block;
    }
    &__sidebar{
      width: 100%;
      margin: 40px 0 0;
    }
  }

  .exchange{
    position: relative;
    &__select{
      display: block;
      position: relative;
      z-index: 4;
      cursor: pointer;
      input{
        cursor: pointer;
      }
    }
    &__top{
      height: 100%;
    }
    &__bottom{
      position: absolute;
      background-color: $mediumDark;
      z-index: 3;
      width: calc(100% - 96px);
      left: 48px;
      top: 154px;
      padding: 56px 0 0;
      border-radius: 8px;
      overflow: hidden;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
    }
    &__items{
      overflow-y: auto;
      height: 100%;
      max-height: 320px;
      padding: 8px;
    }
    &._active{
      .exchange__select{
        pointer-events: none;
        z-index: 6;
      }
      .exchange__bottom{
        z-index: 5;
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .condition{
    margin-top: 40px;
  }

  .main{
    &__group{
      margin-top: 50px;
    }
    &__subtitle{
      margin-bottom: 30px;
    }
    &__bank,
    &__place{
      margin-right: 24px;
    }
  }
}


@media screen and (max-width: 800px){
  .main{
    &__content{
      display: grid;
      grid-template-columns: 100%;
      gap: 40px;
    }
  }

  .exchange{
    width: 100%;
  }
}


@media screen and (max-width: 450px){
  .exchange{
    &__top{
      padding: 20px 24px;
      min-height: unset;
    }
    &__bottom{
      width: calc(100% - 48px);
      left: 24px;
      top: 131px;
    }
  }

  .main-form{
    padding: 24px;
  }

  .main{
    &__banks,
    &__places{
      margin-top: -12px;
    }
    &__bank,
    &__place{
      margin-right: 0;
      width: 100%;
      margin-top: 12px;
    }
  }
}
