@mixin tableTablet() {
  background-color: transparent;

  thead{
    display: none;
  }
  table{
    display: block;
  }
  tbody{
    display: block;
  }
  tr{
    display: grid;
    grid-template-columns: 1.685fr 1fr 1fr;
    border-bottom: 1px solid #404056;
    padding: 24px 0;
    gap: 24px;
    &:first-child{
      border-top: 1px solid #404056;
    }
  }
  td{
    height: unset;
    display: block;
    border: none;
    padding: 0 !important;
    @media screen and (min-width: 576px){
      &:nth-child(2){
        grid-column: 1/2;
        grid-row: 2/3;
      }
      &:nth-child(4){
        grid-column: 2/3;
        grid-row: 1/3;
      }
      &:nth-child(5){
        grid-column: 3/4;
        grid-row: 1/3;
      }
      &:nth-child(3){
        grid-column: 2/4;
      }
      &:last-child{
        grid-column: 1/2;
        grid-row: 3/4;
        text-align: left;
      }
    }

  }
}


@mixin table576(){
  tr{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: unset;
  }
  td{
    &:nth-child(1),
    &:nth-child(2){
      margin-bottom: 5px;
    }
    &:last-child{
      order: 2;
      margin-bottom: 5px;
    }
    &:nth-child(4),
    &:nth-child(5){
      margin: 19px 0 16px;
      width: calc(50% - 12px);
    }
    &:nth-child(4){
      order: 3;
    }
    &:nth-child(5){
      order: 4;
    }
    &:nth-child(3){
      order: 5;
      width: 100%;
    }
  }
}

@mixin table460(){
  td{
    &:nth-child(4),
    &:nth-child(5){
      width: 100%;
      margin: 16px 0 0;
    }
    &:nth-child(5){
      margin-bottom: 12px;
    }
  }
}
