@import "../mixins/table";

.main-form--request{
  .main-form__btn{
    margin-top: 40px;
  }
}

.profile-table--request{
  margin: 0;
  @include tableTablet();
  .profile-table{
    &__title{
      display: unset;
    }
    &__sum{
      background-color: $mediumDark;
      padding: 16px;
      border-radius: 8px;
      &-desc{
        display: block;
        color: $gray;
      }
      &-icon{
        background: $bg;
      }
    }
  }
}

.request{
  flex-grow: 1;
  &__buttons{
    margin-top: 40px;
    width: 100%;
    max-width: 602px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 36px;
  }
  &__info{
    margin-top: 60px;
  }
  &__group{
    &:not(:last-child){
      margin-bottom: 48px;
    }
  }
  &__title{
    margin-bottom: 24px;
  }
  &__desc{
    color: $gray;
    a{
      color: $primary;
      text-decoration: underline;
    }
  }
}


@media screen and (max-width: 992px){
  .request{
    &__buttons{
      max-width: 100%;
      gap: 40px 32px;
    }
    &__group{
      &:not(:last-child){
        margin-bottom: 40px;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .request{
    &__buttons{
      grid-template-columns: 100%;
    }
    &__info{
      margin-top: 40px;
    }
  }

  .profile-table--request{
    @include table576();
  }
}


@media screen and (max-width: 460px){
  .profile-table--request{
    @include table460();
  }
}
