.article{
  &__content{
    color: $gray;
    img{
      width: 100%;
      display: block;
      border-radius: 12px;
      &:not(:last-child){
        margin-bottom: 32px;
      }
    }
  }
  .feedback-card{
    height: unset;
    margin-top: 32px;
  }
}


@media screen and (max-width: 1200px){
  .article{
    .main__sidebar,
    .feedback-card{
      margin-top: 60px;
    }
  }
}

@media screen and (max-width: 576px){
  .article{
    .main__sidebar,
    .feedback-card{
      margin-top: 40px;
    }
  }
}
