.feedback{
  overflow: hidden;
  &__slider{
    margin-top: 40px;
    position: relative;
    &:before,
    &:after{
      content: '';
      position: absolute;
      background: linear-gradient(90deg, rgba(21, 18, 41, 0.39) 0%, #16132C 100%);
      top: 0;
      height: 100%;
      width: 2000px;
      z-index: 2;
      border-radius: 12px;
      pointer-events: none;
    }
    &:before{
      right: 100%;
      margin-right: 32px;
    }
    &:after{
      left: 100%;
      margin-left: 32px;
    }

    .swiper{
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        height: auto;
      }
      &-button{
        &-prev,
        &-next{
          width: 60px;
          height: 60px;
          background-color: $gray;
          border: 2px solid #EEEFF8;
          border-radius: 4px;
          z-index: 3;
          &:hover{
            background-color: darken($gray,20%);
          }
        }
        &-prev{
          left: -72px;
        }
        &-next{
          right: -72px;
        }
      }
    }
  }
  .main__wrap,
  .main-form--contacts{
    margin-top: 100px;
  }
  &__items{
    margin-top: 24px;
  }
}


@media screen and (max-width: 1400px){
  .feedback{
    .swiper{
      &-button{
        &-prev{
          left: -30px;
        }
        &-next{
          right: -30px;
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .feedback{
    &__slider{
      &:before,
      &:after{
        display: none;
      }
    }

    .swiper{
      &-container{
        overflow: hidden;
      }
    }

    .main__wrap,
    .main-form--contacts,
    .main__sidebar{
      margin-top: 60px;
    }
  }
}


@media screen and (max-width: 576px){
  .feedback{
    .swiper{
      &-button{
        &-prev,
        &-next{
          position: static;
          margin: 0 12px;
        }
      }
    }
    &__nav{
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 24px;
    }
    .main__wrap,
    .main-form--contacts,
    .main__sidebar{
      margin-top: 40px;
    }
  }
}


