@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.pagination{
  margin-top: 48px;
  display: flex;
  align-items: center;
  &__item{
    border: 2px solid $mediumDark;
    border-radius: 4px;
    width: 48px;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    &:not(:last-child){
      margin-right: 16px;
    }
    &:hover,
    &--active{
      border-color: $primary;
    }
    &--active{
      pointer-events: none;
    }
  }
}


@media screen and (max-width: 992px){
  .pagination{
    margin-top: 40px;
  }
}

@media screen and (max-width: 576px){
  .pagination{
    justify-content: center;
  }
}
