@import "../mixins/table";

.profile{
  display: grid;
  grid-template-columns: calc(100% - 400px) 400px;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  &__top{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 32px;
    align-items: flex-end;
  }
  &__title{
    margin-top: 40px;
  }
}

.profile-content{
  padding: 40px 60px 60px;
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow-y: auto;
}

.profile-sidebar{
  background-color: $mediumDark;
  display: flex;
  flex-direction: column;
  @media screen and (min-width: 992px){
    .container{
      padding: 0;
    }
  }
  &__top{
    border-radius: 0 0 0 12px;
    border: $border;
    margin: -1px -1px 0 0;
    padding: 40px;
    background-color: $bg;
  }
  &__lk{
    display: flex;
    align-items: center;
  }
  &__btn{
    width: 100%;
    max-width: 320px;
    margin-top: 24px;
  }
  &__info{
    margin-top: 24px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__sale{
    font-weight: 500;
  }
  &__bottom{
  }
  &__links{
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    min-height: 0;
    overflow-y: auto;
    height: calc(100vh - 248px);
    padding: 40px 40px 28px;
  }
  &__link{
    display: flex;
    align-items: center;
    padding: 12px 0;
    &:not(:last-child){
      border-bottom: 1px solid #404056;
    }
    &:first-child,
    &--exit{
      border-top: 1px solid #404056;
    }
    &--exit{
      margin-top: auto;
    }
    &:hover{
      color: $warning;
    }
  }
}

.badge{
  padding: 16px 24px;
  display: flex;
  background-color: $mediumDark;
  border-radius: 8px;
  &__icon{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 16px;
    background-color: $darkGray;
  }
  &__title{
    font-weight: 500;
    font-size: 24px;
    display: block;
  }
  &__desc{
    color: $gray;
  }
}

.profile-table{
  margin-top: 40px;
  flex-grow: 1;
  background: #171727;
  overflow-y: auto;
  min-height: 0;
  table{
    width: 100%;
  }
  th,td{
    border-bottom: 1px solid #202030;
    height: 57px;
    text-align: left;
    &:nth-child(1){
      padding-left: 24px;
    }
    &:last-child{
      text-align: center;
      padding-right: 35px;
    }
  }
  td{
    line-height: inherit;
  }
  &__title{
    display: none;
  }
  &__sum{
    display: flex;
    align-items: center;
    &-icon{
      width: 40px;
      height: 40px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      background: #202030;
      margin-right: 16px;
      flex-shrink: 0;
      img{
        max-width: 100%;
        max-height: 100%;
      }
    }
    &-desc{
      display: none;
    }
  }
  &__status{
    display: inline-flex;
    align-items: center;
    padding: 0 12px;
    border-radius: 20px;
    height: 20px;
    font-weight: 500;
  }
}




@media screen and (max-width: 1470px){
  .profile{
    &__top{
      grid-template-columns: 1fr 1fr;
    }
    &__download{
      height: 100%;
    }
  }

  .profile-table{
    @include tableTablet();
    &__title{
      display: unset;
    }
    &__sum{
      background-color: $mediumDark;
      padding: 16px;
      border-radius: 8px;
      &-desc{
        display: block;
        color: $gray;
      }
      &-icon{
        background: $bg;
      }
    }
  }
}


@media screen and (max-width: 1200px){
  .profile-content{
    padding: 40px;
  }
}


@media screen and (max-width: 992px){
  .profile{
    overflow: unset;
    height: unset;
    grid-template-columns: 100%;
  }

  .profile-sidebar{
    order: -1;
    background-color: transparent;
    position: relative;
    &__top{
      background-color: transparent;
      padding: 16px 0 24px;
      margin: 0;
      border-left: none;
      border-top: none;
      border-right: none;
      border-radius: 0;
    }
    &__inner{
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-bottom: 80px;
      position: relative;
    }
    &__info{
      margin: 0;
    }
    &__btn{
      position: absolute;
      bottom: 0;
      left: 0;
    }
    &__sale{
      margin-right: 40px;
    }
    &__bottom{
      background-color: $darkGray;
      border: 2px solid $blueDark;
      border-radius: 12px;
      margin-right: -24px;
      flex-direction: column-reverse;
      padding: 40px 64px 40px 40px;
      overflow-y: auto;
      &-wrap{
        position: absolute;
        top: calc(100% - 1px);
        right: 0;
        width: 0;
        max-width: calc(100% - 24px);
        overflow: hidden;
        transition: all .6s;
        opacity: 0;
        z-index: 10;
      }
    }
    &__links{
      padding: 0;
      opacity: 0;
      overflow: unset;
      transition-delay: 0s;
    }
    &__link{
      border: none !important;
      text-align: center;
      justify-content: center;
    }
  }

  .profile-content{
    width: 100%;
    max-width: 848px;
    padding: 60px 24px;
    margin: 0 auto;
  }
}


@media screen and (max-width: 576px){
  .profile{
    &__top{
      gap: 24px;
      .badge{
        &:nth-child(3){
          grid-column: 1/-1;
        }
      }
    }
    &__download{
      grid-column: 1/-1;
      height: 73px;
      margin-top: 16px;
    }
    &__title{
      margin-top: 44px;
    }
  }

  .profile-sidebar{
    &__inner{
      flex-wrap: wrap;
      padding: 0;
    }
    &__btn{
      position: static;
      order: 2;
      width: calc(100% - 120px);
      max-width: unset;
    }
    &__info{
      order: 3;
      flex-direction: column;
      align-items: flex-end;
      margin-top: 24px;
    }
    &__sale{
      margin: 0 0 16px;
    }
  }

  .profile-content{
    padding-top: 40px;
    padding-bottom: 40px;
  }

  .profile-table{
    @include table576();
  }
}


@media screen and (max-width: 460px){
  .profile{
    &__top{
      grid-template-columns: 100%;
    }
  }

  .profile-sidebar{
    &__btn{
      width: calc(100% - 100px);
    }
  }


  .profile-table{
    @include table460();
  }
}
