.fg {
  position: relative;
  margin: 0;
  label{
    color: $gray;
    font-weight: 500;
    font-size: 16px;
    display: block;
    margin-bottom: 12px;
  }
  input,
  textarea{
    border: 2px solid #404056;
    background: #404056;
    padding: 0 16px;
    height: 56px;
    width: 100%;
    font-size: 16px;
    color: $gray;
    border-radius: 8px;
    &::placeholder {
      color: $gray;
    }
    &:focus,
    &:active{
      border-color: $primary;
    }
  }
  textarea{
    resize: none;
    padding-top: 24px;
    padding-bottom: 24px;
    height: 152px;
  }
  &__link{
    margin: 8px 0 0 auto;
    font-weight: 500;
    text-decoration-line: underline;
    color: $primary;
    display: block;
    text-align: right;
  }
}

.m-check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        background-image: url(../img/check.svg);
      }
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 24px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    display: flex;
    align-items: center;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 4px;
      border: 1px solid $gray;
      background-repeat: no-repeat;
      background-position: center;
      width: 24px;
      height: 24px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  a{
    position: relative;
    z-index: 2;
    color: $primary;
    text-decoration: underline;
  }
}


.rating {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  &--read-only{
    pointer-events: none;
  }
  &__item {
    transition: all .3s;
    font-size: 38px;
    color: #CFCFCF;
    &:not(:first-child) {
      margin-right: 2px;
    }
    &:hover {
      cursor: pointer;
      color: $warning;
      &~.rating__item {
        color: $warning;
      }
    }
  }
}

.rating[data-total-rating="1"] .rating__item:nth-child(n+5),
.rating[data-total-rating="2"] .rating__item:nth-child(n+4),
.rating[data-total-rating="3"] .rating__item:nth-child(n+3),
.rating[data-total-rating="4"] .rating__item:nth-child(n+2),
.rating[data-total-rating="5"] .rating__item:nth-child(n+1) {
  color: $warning;
}
