.fg {
  select{
    border: 2px solid #404056;
    background: #404056;
    padding: 0 16px;
    height: 56px;
    width: 100%;
    font-size: 16px;
    color: $gray;
    border-radius: 8px;
    &::placeholder {
      color: $gray;
    }
    &:focus,
    &:active{
      border-color: $primary;
    }
  }
}

.profile-sidebar{
  border-radius: 12px;
  position: sticky;
  top: 30px;
  &__top{
    border-radius: 12px;
  }
  &__links{
    height: unset;
  }
}

.lk-content{
  flex-grow: 1;
  &__btn{
    width: 100%;
  }
  .m-btn{
    width: 100%;
  }
  .profile-content{
    padding: 0;
  }
  .profile__top{
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    .badge{
      min-height: 100%;
      grid-column: unset;
    }
  }
}

.userwallets_form_title{
  margin-bottom: 30px;
}

.userwallets_help{
  margin: 10px 0;
  font-size: 16px;
  line-height: 150%;
  p{
    line-height: inherit;
  }
}

.userwallets_one_tab{
  margin-bottom: 60px;
}

.mbtn-accent-outline{
  background-color: transparent;
}

.userwallets_table{
  margin-top: 60px;
}

.userwallets_table_one{
  margin-top: 20px;
}

.stattablediv{
  .profile-table{
    margin-top: 0;
  }
}

.statuserdiv{
  margin-top: 50px;
  &_title{
    font-weight: 500;
    font-size: 24px;
    line-height: 133%;
  }
}

.promouserdiv{
  margin-top: 50px;
  h3{
    font-weight: 500;
    font-size: 24px;
    line-height: 133%;
    margin-bottom: 20px;
  }
  p{
    font-size: 16px;
    line-height: 150%;
    &:not(:last-child){
      margin-bottom: 16px;
    }
  }
  h4{
    color: $gray;
    font-weight: 500;
    font-size: 16px;
    margin-bottom: 12px;
  }
}

.bannerboxtextarea,
.promouserdiv{
  textarea{
    border: 2px solid #404056;
    background: #404056;
    padding: 0 16px;
    height: 56px;
    width: 100%;
    font-size: 16px;
    color: $gray;
    border-radius: 8px;
    &::placeholder {
      color: $gray;
    }
    &:focus,
    &:active{
      border-color: $primary;
    }
  }
  textarea{
    resize: none;
    padding-top: 24px;
    padding-bottom: 24px;
    height: 152px;
  }
}


.promopage{
  ul{
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;
    margin-bottom: 30px;
    li{
      line-height: inherit;
    }
  }
  img{
    max-width: 100%;
  }
}

.pbcontainer {
  &:not(:last-child){
    margin-bottom: 35px;
  }
}

.bannerboxlink{
  margin: 10px 0;
}

.paytext{
  margin-bottom: 40px;
  &_ins{
    font-size: 16px;
    line-height: 150%;
  }
}

.paytable{
  margin-top: 40px;
}

.paydiv{
  .display2{
    margin-bottom: 15px;
  }
}


@media screen and (max-width: 992px){
  .profile-sidebar{
    &__top{
      border: 2px solid #3a2876;
    }
  }
}


@media screen and (max-width: 576px){
  .lk-content{
    .profile__top{
      grid-template-columns: 100%;
    }
    .profile-table{
      th{
        display: block;
        border: none;
        height: unset;
        padding-left: 0;
      }
      td{
        &:last-child{
          text-align: right;
        }
      }
      .ppapitext{
        margin: 10px 0;
        text-align: left;
        &+div{
          text-align: left;
        }
      }
    }
  }
  .profile-sidebar__btn{
    width: 100%;
  }
}
